.ErrorModel {
    position: fixed;
    top: 30vh;
    left: calc(50% - 15rem);
    width: 30rem;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    z-index: 100;
    border-radius: 7px;
  }
  
.ErrorHeading {
    margin: 0;
    padding: 1rem;
    background: #ff2058;
    color: white;
    border-radius: 7px 7px 0 0;
}
  
.ErrorPara{
      padding: 1rem;
  }

.Backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    z-index: 50;
}